* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-form {
  font: inherit;
  min-width: 420px;
  justify-content: center;
  padding: 8px;
  display: flex;
}

.gallery {
  max-width: 1200px;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  margin-left: auto;
  margin-right: auto;
  list-style: none;
  display: grid;
}

.info {
  grid-template-columns: repeat;
  grid-gap: 10px;
  color: #000;
  justify-content: center;
  font-size: 14px;
  text-decoration: none;
  list-style: none;
  display: flex;
}

.info-item {
  margin-top: 0;
  margin-bottom: 10px;
  position: relative;
}

.info-item b {
  text-decoration: none;
  display: block;
}

.galery_image {
  height: 80%;
  width: 100%;
  object-fit: cover;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.galery_image:hover {
  transform: scale(1.03);
}

.galery_link {
  text-align: center;
  height: 100%;
  text-decoration: none;
  display: block;
}

.galery_link:hover {
  cursor: zoom-in;
}

.photo-card {
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.load-more {
  justify-content: center;
  margin: 30px auto;
  display: flex;
}

